  width: 220px;
}
@font-face {
  font-family: 'Poppins';
  src: url('./fonts/Poppins-Bold.ttf') ;}

@font-face {
  font-family: 'PoppinsR';
  src: url('./fonts/Poppins-Regular.ttf') ;}

.cart{
  border: 2px #EF7A45 solid;
  color: #EF7A45;
