  border-radius: 25px;
  cursor: pointer;
  padding: 0px 10px 0px 10px;
  display: flex;
  justify-content: center;
  align-items: center;

  display: flex;
  justify-content: center;
  align-items: center;
}
.cartCont{
  display: flex;
  flex-direction: row;
  justify-content: space-between;
