}
html, body {
height: 100%;
margin: 0;
padding: 0;
}

#nvg {
  width: 325px;
  height: 100%;
  position: fixed;
  top: 0;
  left: 0;
  background-color: #FDDCC3;
}
#all{
  position: absolute;
  left: 320px;
}
.btn{
  width: 270px;
  height: 60px;
  border-radius: 10px;
  margin: 20px 25px 20px 25px;
  /* background-color: #FFC07B; */
  font-family: Poppins;
  font-size: 22px;
  color: white;
  user-select: none;
  display: flex;
  justify-content: center;
  align-items: center;
  cursor: pointer;
}
.emojis{
  width: 25px;
  margin-right: 7px;
}
#sweater{
  width: 180px;
}
.product{
  height: 200px;
  width: 200px;
  object-fit: contain;
}
#search {
  width: 560px;
  height: 50px;
  background-color: #FEECDF;
  border-radius: 25px;
  display: flex;
  align-items: center;
  padding-left: 20px;
  padding-right: 20px;
  font-family: Poppins, sans-serif; 
  font-size: 18px;
  border: none;
}
#searchb{
  background-color: #FEECDF;
  height: 50px;
  width: 50px;
  margin-left: 10px;
  background-image: url("../public/photos/search.png");
  background-repeat: no-repeat;
  background-position: center;
  background-size: 20px;
  background-origin: content-box;
  border: 0;
  border-radius: 25px;
  cursor: pointer;
}

#card{
  height: 50px;
  padding: 0px 25px 0px 25px;
  background-color: #FEECDF;
  border-radius: 25px;
  font-family: Poppins;
  font-size: 18px;
  display: flex;
  align-items: center;
  margin-left: 20px;
  user-select: none;
  cursor: pointer;
}
#top{
  padding: 15px;
  display: flex;
  position: sticky;
  top: 5px;
  z-index: 1;
  background-color: #FFC07B;
  background-color: #F59A3B;
  border-radius: 40px;
  width: min-content;
  margin: 15px;
  margin-bottom: 0px;
  margin-left: 20px;
  width: 1510px;
  
  display: flex;
  justify-content: space-between;
}
#ctn{
  height: 22px;
  margin-right: 10px;
}
#love{
  height: 50px;
  background-color: #FEECDF;
  border-radius: 25px;
  user-select: none;
  display: flex;
  align-items: center;
  justify-content: center;
  font-family: Poppins;
  font-size: 18px;
  padding: 0px 25px 0px 25px;
  user-select: none;
  cursor: pointer;
}
#profil{
  height: 50px;
  padding: 0px 20px 0px 20px;
  background-color: #FEECDF;
  border-radius: 25px;
  float: right;
  display: flex;
  align-items: center;
  justify-content: center;
  margin-left: 20px;
  font-family: Poppins;
  font-size: 18px;
  user-select: none;
  cursor: pointer;
}
#hearte{
  width: 20px;
  margin-right: 7px;
  margin-top: 3px;
}
#account{
  width: 25px;
  margin-right: 5px; 
}
#srch{
  z-index: 1;
  height: 20px;
  position: relative;
  right: 0px;
}
.LoveButton{
  width: 27px;
  position: absolute;
  top: 0;
  right: 0;
  margin: 22px;
  cursor: pointer;
}
.container{
  padding: 20px 0px 0px 30px;
  width: 280px;
}
.Name,.Price{
  font-family: Poppins;
  font-size: 20px;
  margin: 15px;
}

.Name{
  float: left;
  margin-left: 5px;
  margin-bottom: 0px;
  font-weight: 600;
}
.Price{
  float: right;
  margin-right: 0px;
  margin-bottom: 0px;
  background-color: #FDDCC3;
  padding: 5px 10px 5px 10px;
  border-radius: 12px;
  font-weight: 620;
  margin-left: 0px;
}
#logo{
  width: 170px;
  margin: 25px 0px 0px 35px;
}
.filter{
  width: 1535px;
  display: flex;
  justify-content: space-between;
  margin: 20px 0px 0px 15px;
}
.box{
  height: 46px;
  width: min-content;
  white-space: nowrap;
  padding: 0px 20px 0px 20px ;
  margin-left: 15px;
  border-radius: 23px;
  background-color: #FDDCC3;
  font-family: Poppins;
  font-size: 19px;
  display: flex;
  justify-content: center;
  align-items: center;
  user-select: none;
  cursor: pointer;
}
#tier{
  height: 46px;
  width: 150px;
  border-radius: 23px;
  background-color: #FDDCC3;
  margin-left: 170px;
  font-family: Poppins;
  font-size: 19px;
  display: flex;
  justify-content: center;
  align-items: center;
}
.droit{
  display: flex;
  flex-direction: row;
}
#tick{
  width: 20px;
  margin-left: 7px;
}

.box.active {
  background-color: #F59A3B;
  color: white;
  font-weight: 500;
  font-size: 20px;
} 

.box.inactive {
  color: black;
  background-color: #FDDCC3;
}
.icon{
  width: 140px;
  margin: 0px 5px 0px 5px;
  fill:#EF7A45;
}

*{
  user-select: none;
}

.cart:hover{
  .st0{
  fill: white;
}}

#left{
  display: flex;
  flex-direction: row
}
#right{
  display: flex;
  flex-direction: row
}
